@import url(https://fonts.googleapis.com/css?family=Roboto:400,500,700,900&display=swap);

body {
  background-color: #e7efec;
  color: #2f2f2f;
  font-family: 'Roboto', sans-serif;  
}

.flightsPage {
  position: relative;
  background-color: #fff;
  width: 70%;
  margin: 90px auto;
}

.changedDisplayTable {
  width: 100%;
}

.changedDisplayTable label {
  position: absolute;
  height: 20px;
  display: flex;
  justify-content: center;
  padding: 15px 0;
  cursor: pointer;
  top: -50px;
  width: 55%;
  border-top-left-radius: 60px;
  border-top-right-radius: 60px;
  color: white;
  background-color: rgba(30,183,238,.6);
  font-size: 22px;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.changedDisplayTable label:hover {
  background: #1eb7ee;
}

section.buttonChangedDay button.selectedDay {
  color: #1eb7ee;
  border-color: #1eb7ee;
}

span {
  margin-right: 10px;
}

svg {
  width: 40px;
  height: 28px;
}

.up svg {
  -webkit-transform: rotate(55deg);
          transform: rotate(55deg);
}

.changedDisplayTable label:last-child {
  margin-left: 45%;
}

.changedDisplayTable label.selectedDisplay {
  background: white;
  color: #1eb7ee;
  z-index: 1;
}

section.buttonChangedDay {
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  margin-bottom: 20px;
  margin-left: 20px;
}

section.buttonChangedDay button {
  margin-top: 15px;
  background: #fff;
  text-transform: uppercase;
  font-weight: 700;
  font-size: 14px;
  color: rgba(149,152,154,.7);
  cursor: pointer;
  padding: 0 0 10px;
  border: none;
  border-bottom: 3px solid rgba(149,152,154,.7);
  outline: none;
}

section.buttonChangedDay div {
  margin-bottom: 10px;
}

section.buttonChangedDay button:hover {
  color: #1eb7ee;
  border-color: #1eb7ee;
}

section.buttonChangedDay button:nth-child(2) {
  padding-right: 10px;
  padding-left: 10px;
}



table {
  width: 100%;
  border-collapse: collapse;
  text-align: left;
}


tbody tr:nth-child(odd) {
  background: rgba(0,102,135,.1);
}

thead th {
  padding-bottom: 10px;
  padding-top: 15px;
  color: #95989a;
  font-weight: 400;
}

thead th:nth-child(1) {
  padding-left: 10px;
}
 
.terminal span {
  display: flex;
  margin-left: 20px;
  border-radius: 50%;
  height: 30px;
  width: 30px;
  justify-content: center;
  align-items: center;
  font-weight: 700;
  font-size: 18px;
}

.terminalA {
  color:#63c745;
  border: 1px solid #63c745;
}


.terminalD {
  color: #1eb7ee;
  border: 1px solid #1eb7ee;
}

tbody tr:hover {
  box-shadow: 0 5px 10px rgba(0,0,0,.2);
}

tbody td {
  padding: 20px 0; 
}


td.codeShare span,
td.airline span {
  display: flex;
  align-items: center;
  margin-bottom: 5px;
}

td.codeShare span.spCodeShare {
  line-height: 36px;
}


.airlineLogo {
  width: 60px;
  margin-right: 5px;
}
